import React from "react";
import style from "./Projects.module.scss"
import Project from "./project/Project";
import commStyle from '../common/commonStyle/sass/common.module.scss'
import imageWebSite from "../image/img_1.png"
import imageTodoList from "../image/istockphoto-1092571024-170667a.jpg"
import {v1} from "uuid";
import {TitleComponents} from "../common/components/titleComponets/TitleComponents";

type ProjectStateType = {
    id: string,
    title: string,
    image: string,
    link?:string,
    description: string
}
const projectsState: ProjectStateType[] = [
    {
        id: v1(),
        title: "Web Site",
        image: `url(` + imageWebSite + `)`,
        link:"https://dimaer77.github.io/dist/",
        description: "Lorem ipsum dolor sit amet."
    },
    {
        id: v1(),
        title: "To Do List",
        image: `url(` + imageTodoList + `)`,
        link:"##",
        description: "Lorem ipsum dolor sit amet."
    }
]

export function Projects() {
    return (
        <div className={commStyle.commonBlock}>
            <div className={`${commStyle.commonContainer} ${style.worksContainer}`}>
                <TitleComponents title={"My Project"}/>
                <div className={style.works}>
                    {projectsState.map(el => {
                        return <Project key={el.id} link={el.link} nameProject={el.title} image={el.image} shortDesc={el.description}/>
                    })}
                </div>
            </div>
        </div>
    )
}

export default Projects;
